<template>
  <div>
    <el-affix :offset="80">
      <div class="feed-nav">
        <a-button class="back-btn" @click="back">返回
          <template #icon>
            <LeftOutlined/>
          </template>
        </a-button>

        <span
            :style="{fontSize:'18px',marginLeft:'270px',borderRadius:'8px',backgroundColor:'#c2c2c2',padding:'5px 20px'}">{{
            feed.userNickName
          }}</span>

      </div>
    </el-affix>

    <a-card :loading="loading" class="post" size="default">
      <a-card-meta>
        <template #title>
          <div class="post-nickname" @click="toUserCenter(feed.userId)" style="cursor: pointer">{{
              feed.userNickName
            }}
          </div>
        </template>

        <template #description>
          <div class="post-createtime">{{ feed.createTime }}</div>
        </template>

        <template #avatar>
          <a-avatar :size="60" :src="feed.userAvatar" @click="toUserCenter(feed.userId)"
                    style="cursor: pointer"></a-avatar>
        </template>
      </a-card-meta>

      <div class="post-content">
        <a-typography-paragraph
            :style="{lineHeight: '25px',fontSize:'16px'}" :content="feed.content">
          <template #ellipsisSymbol>
            <span :style="{color:'#de1c31'}">展开</span>
          </template>
        </a-typography-paragraph>
      </div>


      <a-image
          :src="feed.imageUrl"
          :lazy="true"
      />

      <template #actions>
        <a-button>
          <template #icon>
            <ShareAltOutlined key="forward"/>
          </template>
        </a-button>

        <a-button>
          <template #icon>
            <CommentOutlined key="comment"/>
          </template>
          {{ feed.comments.length }}
        </a-button>


        <a-button @click="unLikeFeed"
                  v-if="feed.likedUserIds.includes(store.userInfo.userId)"
        >
          <template #icon>
            <LikeOutlined style="color: #de1c31" key="like"/>
          </template>
          {{ feed.likedUserIds.length }}
        </a-button>
        <a-button @click="likeFeed" v-else>
          <template #icon>
            <LikeOutlined key="like"/>
          </template>

          {{ feed.likedUserIds.length }}
        </a-button>
      </template>
    </a-card>
  </div>


  <div class="post-comment-form">
    <a-form style="">
      <a-form-item>
        <a-textarea
            :auto-size="{ minRows: 5, maxRows: 6 }"
            placeholder="友善发言，文明评论~"
            v-model:value="postComment.content"
            id="postCommentInput"
            @keydown.enter.native="submitComment"
        />
      </a-form-item>
      <a-form-item>
        <a-popover trigger="click" placement="bottom">
          <a-button class="face-btn">表情
            <template #icon>
              <SmileOutlined/>
            </template>
          </a-button>
          <template #content>
            <EmojiPicker
                :native="true"
                v-show="true"
                :i18n="i18n"
                @select="onSelectEmoji"
            />
          </template>
        </a-popover>
        <a-button style="float: right" type="primary" @click="submitComment" :disabled="postComment.content === ''">
          评论
        </a-button>
      </a-form-item>
    </a-form>
  </div>


  <a-comment v-for="(item,index) in firstLevelComment" :key="index">
    <template #actions>
      <span @click="beforeReply(item)">回复</span>
    </template>
    <template #author>
      <a @click="toUserCenter(item.userId)">{{ item.userNickName }}</a>
    </template>
    <template #avatar>
      <a-avatar @click="toUserCenter(item.userId)" :src="item.userAvatar" :alt="item.userNickName"/>
    </template>
    <template #content>
      <p>
        {{ item.content }}
      </p>
    </template>
    <div v-for="(r,i) in notFirstLevelComment" :key="i">
      <a-comment v-if="r.toCommentId === item.commentId">
        <template #actions>
          <span @click="beforeReply(r)">回复</span>
        </template>
        <template #author>
          <a @click="toUserCenter(r.userId)" style="font-size: 14px">{{ r.userNickName }}</a><a
            @click="toUserCenter(r.toUserId)"><span style="color: #16acff"> @ {{ r.toUserNickName }}</span></a>
        </template>
        <template #avatar>
          <a-avatar @click="toUserCenter(r.userId)" :src="r.userAvatar" :alt="r.userNickName"/>
        </template>
        <template #content>
          <p>
            {{ r.content }}
          </p>
        </template>
      </a-comment>
    </div>
  </a-comment>


  <a-modal v-model:open="openRep" width="60%" :footer="null" :closable="false">
    <div class="rep-box">
      <a-textarea
          class="rep-con"
          v-model:value="replyContent"
          placeholder="友善发言，文明评论~"
          :auto-size="{ minRows: 3, maxRows: 5 }"
          id="repCommentInput"
          @keydown.enter.native="replyComment"
      />
    </div>
    <a-form-item>
      <a-popover trigger="click" placement="bottom">
        <a-button class="face-btn">表情
          <template #icon>
            <SmileOutlined/>
          </template>
        </a-button>
        <template #content>
          <EmojiPicker
              :native="true"
              v-show="true"
              :i18n="i18n"
              @select="onSelectEmojiReply"
          />
        </template>
      </a-popover>
      <a-button style="float: right" type="primary" @click="replyComment()" :disabled="replyContent === ''">回复
      </a-button>
    </a-form-item>
  </a-modal>


</template>

<script setup>
import {CommentOutlined, LeftOutlined, LikeOutlined, ShareAltOutlined, SmileOutlined} from "@ant-design/icons-vue"
import {store} from "@/store";
import router from "@/router";
import {onMounted, ref} from "vue";
import requestService from "@/utils/request";
import {notification} from "ant-design-vue";

const back = () => {
  router.go(-1);
}
const feedId = ref('');
const openRep = ref(false)


import EmojiPicker from 'vue3-emoji-picker'
import 'vue3-emoji-picker/css'

const i18n = ref({
  search: '请输入关键字',
  categories: {
    Activity: "Atividades",
    Flags: "旗帜",
    Foods: "食物",
    Frequently: "Frequentes",
    Objects: "Objetos",
    Nature: "Natureza",
    Peoples: "Pessoas",
    Symbols: "Símbolos",
    Places: "Locais"
  }
})

function onSelectEmoji(emoji) {

  let input = document.getElementById("postCommentInput")
  let startPos = input.selectionStart
  let endPos = input.selectionEnd
  let resultText = input.value.substring(0, startPos) + emoji.i + input.value.substring(endPos)
  input.value = resultText
  input.focus()
  input.selectionStart = startPos + emoji.n.length
  input.selectionEnd = startPos + emoji.n.length
  postComment.value.content = resultText

}

function onSelectEmojiReply(emoji) {

  let input = document.getElementById("repCommentInput")
  let startPos = input.selectionStart
  let endPos = input.selectionEnd
  let resultText = input.value.substring(0, startPos) + emoji.i + input.value.substring(endPos)
  input.value = resultText
  input.focus()
  input.selectionStart = startPos + emoji.n.length
  input.selectionEnd = startPos + emoji.n.length
  replyContent.value = resultText

}


onMounted(() => {
  feedId.value = router.currentRoute.value.query.feedId;
  getFeed();
})
const toUserCenter = (userId) => {
  router.push({path: '/userCenter/otherUserCenter', query: {userId: userId}})
}
const replyContent = ref('')
const postComment = ref({
  toCommentId: '',
  content: '',
  feedId: '',
  userId: '',
  userAvatar: '',
  userNickName: '',
  toUserId: '',
  toUserNickName: '',
  createTime: '',
  updateTime: ''
})
const submitComment = () => {
  if (postComment.value.content === '') {
    openNotificationWithIcon('error', '评论内容不能为空')
    return;
  }
  postComment.value.feedId = feedId.value;
  postComment.value.toCommentId = '0';
  postComment.value.toUserId = '0';
  postComment.value.toUserNickName = '';
  postComment.value.userAvatar = store.userInfo.userAvatar;
  postComment.value.userId = store.userInfo.userId;
  postComment.value.userNickName = store.userInfo.userNickName;

  requestService.post("/comment", postComment.value).then((res) => {
    if (res.data.code === 3001) {
      postComment.value.content = '';
      openNotificationWithIcon('success', res.data.msg)
      getFeed();
      requestService.post("/mail/sendCommentHtmlMail", {
            to: feed.value.userEmail,
            subject: '邮件通知',
            title: '新评论',
            nickName: feed.value.userNickName,
            fromName: store.userInfo.nickName
          },
          {
            headers: {'Content-Type': 'application/x-www-form-urlencoded'}
          }
      ).then((res) => {
        console.log(res)
      })
    } else {
      openNotificationWithIcon('error', res.data.msg)
    }
  })

}

const beforeReply = (comment) => {
  openRep.value = true;
  postComment.value.feedId = feedId.value;
  if (comment.toCommentId === '0') {
    postComment.value.toCommentId = comment.commentId;
  } else {
    postComment.value.toCommentId = comment.toCommentId;
  }
  postComment.value.toUserId = comment.userId;
  postComment.value.toUserNickName = comment.userNickName;
  postComment.value.userAvatar = store.userInfo.userAvatar;
  postComment.value.userId = store.userInfo.userId;
  postComment.value.userNickName = store.userInfo.userNickName;
}
const replyComment = () => {
  if (replyContent.value === '') {
    openNotificationWithIcon('error', '评论内容不能为空')
    return;
  }
  postComment.value.content = replyContent.value;

  requestService.post("/comment", postComment.value).then((res) => {
    if (res.data.code === 3001) {
      postComment.value.content = '';
      replyContent.value = '';
      openNotificationWithIcon('success', res.data.msg)
      openRep.value = false;
      getFeed();
    } else {
      openNotificationWithIcon('error', res.data.msg)
    }
  })
}

const firstLevelComment = ref([
  {
    commentId: '',
    toCommentId: '',
    content: '',
    feedId: '',
    userId: '',
    userAvatar: '',
    userNickName: '',
    toUserId: '',
    toUserNickName: '',
    createTime: '',
    updateTime: ''
  }
]);
const notFirstLevelComment = ref([
  {
    commentId: '',
    toCommentId: '',
    content: '',
    feedId: '',
    userId: '',
    userAvatar: '',
    userNickName: '',
    toUserId: '',
    toUserNickName: '',
    createTime: '',
    updateTime: ''
  }
]);

let feed = ref({
  comments: [
    {
      commentId: '',
      toCommentId: '',
      content: '',
      feedId: '',
      userId: '',
      userAvatar: '',
      userNickName: '',
      toUserId: '',
      toUserNickName: '',
      createTime: '',
      updateTime: ''
    }
  ],
  likedUserIds: [],
  content: '',
  createTime: '',
  feedId: '',
  imageUrl: '',
  updateTime: '',
  userAvatar: '',
  userEmail: '',
  userId: '',
  userNickName: ''
})
const loading = ref(true);

const likedCount = ref(0);
const commentCount = ref(0);


const getFeed = () => {
  requestService.get("/feed/" + feedId.value).then((res) => {
    if (res.data.code === 2001) {
      feed.value = res.data.data;
      loading.value = false;
      firstLevelComment.value = feed.value.comments.filter((item) => {
        return item.toUserId === '0'
      })
      notFirstLevelComment.value = feed.value.comments.filter((item) => {
        return item.toUserId !== '0'
      })
    }
  })
}

const likeFeed = () => {
  requestService.post("/like",
      {
        feedId: feedId.value,
        feedUserId: feed.value.userId
      },
      {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then((res) => {
    if (res.data.code === 3001) {
      feed.value.likedUserIds.push(store.userInfo.userId)
      openNotificationWithIcon('success', res.data.msg)
    } else {
      openNotificationWithIcon('error', res.data.msg)
    }
  })
}

const unLikeFeed = () => {
  requestService.delete("/like",
      {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'},
        data: {
          feedId: feedId.value,
          feedUserId: feed.value.userId
        }
      }
  ).then((res) => {
    if (res.data.code === 5001) {
      feed.value.likedUserIds.splice(feed.value.likedUserIds.indexOf(store.userInfo.userId), 1)
      openNotificationWithIcon('success', res.data.msg)
    } else {
      openNotificationWithIcon('error', res.data.msg)
    }
  })
}

const openNotificationWithIcon = (type, title, description) => {
  notification[type]({
    message: title,
    description: description,
    duration: 2
  });
};
</script>

<style scoped>

.feed-nav {
  border-radius: 5px;
  background-color: white;
  padding: 10px;
}

.post {
  margin-bottom: 20px;
  min-width: 790px;
  max-width: 790vw;
  width: 790px;
}

.post button {
  border: none;
  box-shadow: none;
}

.post button:hover {
  color: #de1c31;
}

/*消除按钮点击样式*/
:global(.ant-wave) {
  display: none !important;
}

.post-nickname {
  font-size: 19px;
  min-width: 100px;
}

.post-createtime {
  font-size: 15px;
}

.post-content {
  margin: 20px 0;
  white-space: pre-wrap;
}

.rep-box {
  margin-bottom: 20px;
  padding: 15px 10px;
  background-color: white;
}

.rep-con {
  padding: 10px;
}

.back-btn {
  background-color: rgba(0, 0, 0, 0);
  border: black;
  font-size: 16px;
  box-shadow: none;
}

.back-btn:hover {
  color: #de1c31;
}

.face-btn {
  position: absolute;
  margin-left: 620px;
  border: none;
  box-shadow: none;
}

.face-btn:hover {
  color: #de1c31;
}
</style>